.header
{
    background-image: linear-gradient(to right, transparentize($primario, .1) 0%, transparentize($primario, .1) 100%), url('../img/header_bg.webp');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 3rem 0;
    position: relative;

    @include tablet 
    {
        padding: 3rem 0 20rem 0;
    }

    &__barra
    {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include tablet 
        {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo
    {
        width: 25rem;
    }

    &__contenido
    {
        
        @include tablet 
        {
            margin-top: 10rem;
            max-width: 50rem;
        }
    }

    &__heading
    {
        color: $blanco;
        text-align: center;

        @include tablet 
        {
            text-align: left;
        }
    }

    &__texto
    {
        color: $blanco;
        display: none;

        @include tablet 
        {
            display: block;
        }
    }

    &__imagen
    {
        display: none;

        @include tablet 
        {
            display: block;
            position: absolute;
            right: 0;
            bottom: -5rem;
            max-width: 35rem;
            
            border-top-left-radius: 2rem;
            border-bottom-left-radius: 5rem;
        }

        @include desktopXL 
        {
            max-width: 60rem;
        }
    }
}