
/** Media Queries **/
@mixin telefono {
    @media (min-width: $telefono) {
        @content;
    }
}
@mixin telefonoXL {
    @media (min-width: $telefonoxl) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: $tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: $desktop) {
        @content;
    }
}
@mixin desktopXL {
    @media (min-width: $desktopxl) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;

    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin heading($salto: false)
{
    font-weight: 400;

    span
    {
        font-weight: 700;
        
        @if($salto)
        {
            display: block;
        }
    }

    &::after
    {
        content: '';
        display: block;
        height: 1rem;
        width: 15rem;
        margin-top: 1rem;
        background-image: linear-gradient( to right, $primario 0%, $primario 50%, lighten($primario, 20%) 50%, lighten($primario, 20%) 100% );
    }
}
