.navegacion
{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;

    @include tablet 
    {
        flex-direction: row;
        gap: 2rem;
        margin-top: 0;
    }

    &__link
    {
        color: $blanco;
        font-size: 1.8rem;
        margin-bottom: 2rem;

        &:last-of-type
        {
            margin-bottom: 0;
        }

        @include tablet 
        {
            margin-bottom: 0;
        }
    }
}