.footer
{
    background-color: $primario;
    padding-top: 5rem;
    margin-top: 5rem;

    &__grid
    {
        text-align: center;
        @include tablet 
        {
            @include grid(3, 2rem);
        }
    }

    &__heading
    {
        color: $blanco;
        font-size: 2rem;
        text-transform: uppercase;
    }

    &__texto
    {
        color: $blanco;
    }

    &__logo
    {
        width: 25rem;
    }

    &__box
    {

        &--imagen
        {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    &__navegacion
    {
        display: flex;
        flex-direction: column;
    }

    &__link
    {
        color: $blanco;
        font-size: 1.8rem;
        margin-bottom: 1rem;

        &:last-of-type
        {
            margin-bottom: 0;
        }
    }

    &__copyright
    {
        background-color: darken($primario, 10%);
        text-align: center;
        padding: 1rem;  
        margin: 5rem 0 0 0;  
        color: $blanco;
    }
}