.propiedades
{
    margin-top: 5rem;

    @include tablet 
    {
        margin-top: 10rem;
    }

    &__heading
    {
        @include heading;
    }

    &__grid
    {
        @include grid(1, 2rem);

        @include tablet 
        {
            @include grid (3, 2rem);
        }
    }
}

.propiedad
{
    background-color: $blanco;

    &__imagen
    {
        aspect-ratio: 10 / 6 ;
        background-size: auto 150%;

        position: relative;
    }

    @for $i from 1 through 6 
    {
        $imagen : '../img/propiedad_' + $i + '.webp';
        &:nth-child(#{$i}) &__imagen
        {
            background-image: linear-gradient(to bottom, transparentize( $negro, 1) 0%, transparentize( $negro, .3%) 100%, ), url($imagen);
        }
    }

    &__nombre
    {
        position: absolute;
        left: 1rem;
        bottom: 1rem;

        color: $blanco;
        font-size: 2.3rem;
    }

    &__contenido
    {
        padding: 2rem;
    }

    &__precio
    {
        font-size: 3rem;
        margin: 0;
    }

    &__texto
    {
        margin-bottom: 0;
    }

    &__enlace
    {
        @include boton($primario, $blanco);
        display: block;
        text-transform: uppercase;
    }
}