.destacada
{
    margin-top: 5rem;

    background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, $primario 50%, $primario 100%), url(../img/destacada.webp);
    background-repeat: no-repeat;
    background-position: center, -100rem -30rem;
    background-size: 100%, cover;

    @include telefono 
    {
        background-position: center, -80rem -30rem;
    }

    @include telefonoXL 
    {
        background-position: center, -60rem -30rem;
    }

    @include tablet 
    {
        background-image: linear-gradient(to right, transparent 0%, transparent 50%, $primario 50%, $primario 100%), url(../img/destacada.webp);
        background-position: center, -50rem center;
    }

    &__grid
    {
        display: grid;
        grid-template-rows: repeat(2, 1fr);

        @include tablet 
        {
            grid-template-rows: unset;
            grid-template-columns: repeat(2, 1fr);
            align-items: center;
        }
    }

    &__contenido
    {
        padding: 5rem;
        grid-row: 2 / 3;

        @include tablet 
        {
            padding: 10rem 5rem;
            grid-column: 2 / 3;
            grid-row: unset;
        }
    }

    &__heading
    {
        color: $blanco;
        text-transform: uppercase;
    }

    &__texto
    {
        color: $blanco;
        font-size: 1.8rem;
    }

    &__precio
    {
        color: $blanco;
        font-size: 2.4rem;
        font-weight: 700;
    }

    &__enlace
    {
        @include boton($blanco, $primario);
    }
}